<template>
  <div class="perCapitaSales">
    <van-row class="sub-header">
      <van-col class="text-left" span="18">
        <span
        v-for="(item, index) of tabList"
        :key="index"
        :class="active === index ? 'btn-left btn-active' : 'btn-left'"
        @click="activeTab(item, index)">
        {{ item.label }}
        </span>
      </van-col>
      <van-col class="text-right" span="6">
        <span class="btn-right" @click="handleRightBtn">{{ baseInfo.rightBtn }}</span>
      </van-col>
    </van-row>
    <van-row style="height: 5px;"></van-row>
    <title-info :title="showPart.title" :remark="showPart.remark"></title-info>
  </div>
</template>

<script>
import { NON_DYNAMIC_HEIGHT, PROMPT } from '@/utils'
import TitleInfo from '@/components/BaseTitleInfo'

export default {
  name: 'PerCapitaSales',
  components: { TitleInfo },
  data () {
    return {
      PROMPT, // 提示信息合辑
      baseInfo: { // 界面基础设置
        rightBtn: '导航2_0' // 右上方按钮（单个）
      },
      showPart: { // 需渲染数据
        title: '',
        remark: [],
        tableData: []
      },
      active: 0, // 选中状态
      // freshTime: '', // 备注-更新时间
      tabList: [{
        label: '类型1(默认)', // tab标签名
        title: '标题1：多类别报表切换（表格字段不一）', // 标题
        remark: [''], // 备注
        valInx: 0, // 含变量备注的位置（行数-1）
        tableData: [] // 表格数据
      }, {
        label: '类型2',
        title: '标题02：多类别报表切换',
        remark: ['(第二行备注)', ''],
        valInx: 1,
        tableData: []
      }, {
        label: '类型3',
        title: '标题003：多类别报表切换',
        remark: [''],
        valInx: 0,
        tableData: []
      }]
    }
  },
  computed: {
    height () { // 表格高度 = 非动态高度 - 备注高度 - 本组件的占位高度
      return NON_DYNAMIC_HEIGHT - 18 * this.remark.length - 5
    }
  },
  beforeMount () {
    this.init()
  },
  methods: {
    init () {
      this.showPart = this.tabList[this.active]
      this.showPart.remark[0] = '更新时间： ' + new Date().toLocaleString()
    },
    handleRightBtn () { // 点击右上方按钮（按钮操作与当前页面数据对应）
      alert(this.baseInfo.rightBtn)
    },
    activeTab (item, index) { // tab选中
      this.baseInfo.rightBtn = '导航2_' + index
      this.freshTime = Math.random() // 模拟
      this.active = index // 标记选中状态
      this.showPart = item // 替换需显示数据
      this.showPart.remark[item.valInx] = '更新时间： ' + this.freshTime // 处理备注数据
    }
  }
}
</script>

<style>
/* 按钮选中样式-g */
.btn-active {
  color: orange;
}
</style>



